body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#pic{
    float: left
}

.buttonC{
    margin-top: 30px
}
.title{
    margin-top: 40px;
    margin-bottom: 40px
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.yeet{
    float: right!
}

